.float-bloc-graph {
    margin-top: 15%;
    width: 49%;
    height: auto;
    background-color: #fff;
    border-radius: 51px;
    filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.161));
    padding: 20px; /* Ajoutez un padding pour espacer le contenu */
}

.float-bloc-stat {
    margin-left: 2%;
    margin-top: 15%;
    width: 49%;
    height: auto;
    background-color: #F0FFE2;
    border-radius: 51px;
    filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.161));
    padding: 20px; /* Ajoutez un padding pour espacer le contenu */
}

.greet {
    color: #2b2e4a;
}

